<template>
  <div class="scrollBox">
    <header-nav :msg="msg" v-if="$utils.showNavBar()"></header-nav>
    <div id="activityDetail" :class="{hasHeader: $utils.showNavBar()}">
      <!-- 提交截图 -->
      <div v-if="!details || details.audit_status === 0">
        <div class="activity-title">请上传3张应用市场好评截图</div>
        <div class="img-list">
          <div class="iconSize" v-for="(item, index) in previewImgList" :key="index">
            <div class="imgWrapper">
              <img :src="item" preview="1" alt="图片">
            </div>
            <div class="deleteIcon" @click="deleteImage(index)"></div>
          </div>
          <div class="upload" v-if="previewImgList.length < 3">
            <div class="photo">
              <img src="../../../assets/img/personal/photo.png" alt="上传">
            </div>
            <div class="tips">{{previewImgList.length > 0 ? `${previewImgList.length} / 3` : '添加图片'}}</div>
            <label class="labelContent" for="inputFile">
              <input class="uploadFile" id="inputFile" @change="uploadFile" multiple="multiple" accept="image/*" type="file">
            </label>
          </div>
        </div>
        <div class="rect"></div>
        <div class="inputText">
          <div class="activity-title">截图备注说明（必填）</div>
          <div class="activity-content">
            <textarea class="activity-content" placeholder="请详细描述这是哪几个应用市场的截图，以便审核通过，5-100字" maxlength="100" v-model="content">
            </textarea>
            <span class="count">{{content.length}} / 100</span>
          </div>
          <div class="activity-button" @click="confirmFn" :class="{active: content.length > 0}">提交</div>
        </div>
      </div>
      <!-- 审核中 -->
      <div v-else-if="details.audit_status === 1 || details.audit_status === 3">
        <div class="a-title">您已提交截图，<span v-if="$route.query.endTime">{{$utils.timestampToStr(new Date($route.query.endTime.replace(/-/g, '/')), 'Y-M-D')}}</span>审核完毕。</div>
        <div class="a-user">
          <div class="avatar iconSize">
            <img :src="commonState.userInfo.avatar" alt="头像">
          </div>
          <div class="nickname">{{$utils.splitStr(commonState.userInfo.nickname, 8)}}</div>
        </div>
        <div class="rect"></div>
        <div class="second-title">提交截图</div>
        <div class="img-list second border-1px">
          <div class="iconSize" v-for="(item, index) in details.picture_urls" :key="index">
            <div class="imgWrapper">
              <img :src="item" preview="1" alt="图片">
            </div>
            <!-- <div class="deleteIcon" @click="deleteImage(index)"></div> -->
          </div>
        </div>
        <div class="second-title">备注信息</div>
        <div class="remark">{{details.content}}</div>
      </div>
      <!-- 审核完成 -->
      <div class="overflow" v-else-if="details.audit_status === 2">
        <div class="success-icon iconSize">
          <img src="../../../assets/img/personal/success.png" alt="审核成功">
        </div>
        <div class="o-title">你的截图通过审核</div>
        <div class="o-sec-title o-tip">系统已将本次活动奖励发放到你的掘金宝账户<br>里，请及时登录掘金宝APP查看。</div>
        <div class="o-third o-tip">希望你继续参与下次平台活动！</div>
      </div>
    </div>
    <prompt-box
      :dropBol="showFlag"
      @leftBtn="showFlag=false"
      @rightBtn="submit"
      rightTxt="提交"
      contentTxt="是否提交截图进行审核"
    >
    </prompt-box>
    <not-pass v-if="showPass" @reEdit="reEdit" :details="details"></not-pass>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import PromptBox from '../../common/prompt-box'
import NotPass from './not-pass'
import {mapState} from 'vuex'
export default {
  computed: mapState({
    commonState: state => state.common
  }),
  components: {
    HeaderNav,
    PromptBox,
    NotPass
  },
  data () {
    return {
      imgList: [], // 上传的图片列表
      details: {},
      content: '',          // 提交备注
      msg: '',              // 标题
      showFlag: false,      // 显示确认提示弹框
      showPass: false,        // 显示管理员回复内容
      previewImgList: [],     // 预览图片对象
      fileList: []            // 文件对象
    }
  },
  methods: {
    deleteImage (index) {
      this.fileList.splice(index, 1)
      this.previewImgList.splice(index, 1)
      // this.imgList.splice(index, 1)
    },
    // 重新编辑
    reEdit () {
      this.$set(this.details, 'audit_status', 0)
      this.setTitle()
      this.showPass = false
    },
    uploadFile (e) {
      if (!this.$utils.isImg(e)) {
        this.$toast({
          message: '请上传图片',
          position: 'middle',
          duration: 1500
        })
        return
      }
      if (e.target.files) {
        this.fileList = [...this.fileList, ...e.target.files].splice(0, 3)
        this.previewImgList = [...this.previewImgList, ...this.$utils.getPreviewList(e.target.files)].splice(0, 3)
        this.$previewRefresh()
      }
    },
    confirmFn () {
      if (this.previewImgList.length < 3) {
        return this.$toast({
          message: '请上传三张应用市场好评截图',
          duration: 1500
        })
      } else if (!this.content || this.content.trim() === '') {
        return this.$toast({
          message: '请输入截图备注说明',
          duration: 1500
        })
      } else if (this.content.trim().length < 5) {
        this.$toast({
          message: '截图备注不得小于五个字符'
        })
      } else {
        this.showFlag = true
      }
    },
    submit () {
      let data = {
        activity_id: this.$route.params.id,
        content: this.content.trim()
      }
      this.$indicator.open({
        text: '提交中，请稍后'
      })
      setTimeout(() => {
        this.$indicator.close()
      }, 3000)
      this.$utils.uploaderMuti(this.$utils.cache('user_token'), 'activity', this.fileList).then(imgList => {
        this.$indicator.close()
        data.picture_urls = imgList.join(',')
        this.$api.post('ACTIVITY_APPLY', data, res => {
          this.details = {
            audit_status: 1,
            content: data.content,
            picture_urls: this.previewImgList
          }
          this.$previewRefresh()
          this.showFlag = false
          this.$toast({
            message: '上传成功',
            duration: 1500
          })
        })
      }).catch(() => {
        this.$toast('上传失败')
        this.$indicator.close()
      })
    },
    setTitle () {
      if (this.details.audit_status === 0) {
        this.msg = '上传截图'
      } else if (this.details.audit_status === 1) {
        this.msg = '审核状态'
      } else if (this.details.audit_status === 3) {
        this.msg = '审核结果'
        this.showPass = true
      } else if (this.details.audit_status === 2) {
        this.msg = '审核结果'
      }
      if (this.msg) {
        document.title = this.msg
      }
    },
    getDetail (cb) {
      let data = {
        activity_id: this.$route.params.id
      }
      this.$api.get('ACTIVITY_DETAIL', data, res => {
        this.details = res.data
        this.$previewRefresh()
        typeof cb === 'function' && cb(res.data)
      })
    }
  },
  mounted () {
    if (!this.commonState.login_bol) {
      this.$router.replace('/')
    }
    this.getDetail(res => {
      this.setTitle()
    })
  }
}
</script>
<style lang="scss" scoped>
#activityDetail {
  overflow: auto;
  height: 100vh;
  -webkit-overflow-scrolling: touch;
  .overflow {
    overflow: hidden;
    .success-icon {
      width: 124px;
      height: 124px;
      margin: 50px auto;
    }
    .o-title {
      font-size: 32px;
      text-align: center;
      line-height: 45px;
      color: #333333
    }
    .o-tip {
      font-size: 28px;
      color: #666666;
      line-height: 40px;
      text-align: center;
    }
    .o-sec-title {
      padding: 30px;
    }
  }
  .activity-title {
    padding: 30px 30px 50px;
    font-size: 32px;
    color: #333333;
  }
  .img-list {
    padding: 0 30px 30px;
    display: flex;
    flex-flow: nowrap;
    .iconSize {
      background: #D8D8D8;
      width: 133px;
      height: 133px;
      margin-right: 30px;
      position: relative;
      .deleteIcon {
        width: 40px;
        height: 40px;
        background:  url('../../../assets/img/personal/close.png') no-repeat center;
        background-size: 100%;
        right: -20px;
        top: -20px;
        position: absolute;
        cursor: pointer;
      }
      .imgWrapper {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
        >img {
          width: 100%;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          height: auto;
        }
      }
    }
  }
  .rect {
    height: 20px;
    background: #f5f5f5;
  }
  .upload {
    width: 133px;
    height: 133px;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    border: 1px dashed #999;
    position: relative;
    label {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
    }
    .photo {
      width: 49px;
      height: 43px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .tips {
      font-size: 24px;
      margin-top: 20px;
      color: #999;
    }
    input[type=file] {
      // display: none;
      width: 0;
      height: 0;
      overflow: hidden;
      visibility: hidden;
    }
  }
  .activity-content {
    position: relative;
    width: 690px;
    margin: 0 auto;
    textarea {
      width: 690px;
      height: 292px;
      border-radius: 10px;
      display: block;
      box-sizing: border-box;
      padding: 20px 20px 50px;
      user-select: text;
      line-height: 1.5;
      font-size: 28px;
      color: #333;
    }
    .count {
      position: absolute;
      color: #333;
      height: 30px;
      font-size: 28px;
      line-height: 30px;
      bottom: 20px;
      right: 20px;
      z-index: 2;
    }
  }
  .activity-button {
    margin: 111px auto 50px;
    width: 471px;
    height: 80px;
    line-height: 80px;
    text-align: center;
    background: #F4A82B;
    border-radius: 40px;
    color: #fff;
  }
  // 状态2
  .a-title {
    font-size: 28px;
    color: #FF7F18;
    line-height: 70px;
    height: 70px;
    text-align: center;
    background: #FCEAC5;
  }
  .a-user {
    padding: 30px;
    text-align: center;
  }
  .avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin: 0 auto;
    overflow: hidden;
  }
  .nickname {
    padding-top: 24px;
    color: #666;
    line-height: 40px;
  }
  .second-title {
    padding: 30px 30px 20px;
    line-height: 45px;
    font-size: 32px;
  }
  .remark {
    border: 1px solid #999999;
    border-radius: 10px;
    color: #666;
    font-size: 28px;
    min-height: 133px;
    width: 690px;
    margin: 0 auto;
    line-height: 1.5;
    padding: 10px 20px;
    box-sizing: border-box;
  }
}
</style>



