<template>
  <div class="maskBox" style="z-index: 5">
    <div class="passContent">
      <div class="close" @click="close()"></div>
      <div class="title">截图未通过审核</div>
      <div class="p-content">很抱歉，你的截图未通过审核，原因<br>如下，请编辑后重新提交：</div>
      <div class="c-content">{{details.audit_result}}</div>
      <div class="c-button" @click="reEdit">好的</div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {}
  },
  props: {
    details: {
      type: Object
    }
  },
  methods: {
    reEdit () {
      this.$emit('reEdit', true)
    },
    close () {
      this.$emit('reEdit', false)
    }
  }
}
</script>

<style lang="scss" scoped>
.passContent {
  width: 569px;
  height: 719px;
  .title {
    padding: 50px 0 22px;
    font-size: 32px;
    color: #FF4112;
    text-align: center;
  }
  .p-content {
    font-size: 28px;
    color: #666666;
    line-height: 38px;
    text-align: center;
  }
  .c-content {
    background: #F9F9F9;
    border: 1px solid #F4A82B;
    border-radius: 10px;
    width: 442px;
    margin: 42px auto 70px;
    line-height: 1.5;
    text-align: justify;
    box-sizing: border-box;
    height: 290px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding: 10px;
  }
  .c-button {
    background: #F4A82B;
    border-radius: 40px;
    width: 380px;
    line-height: 80px;
    height: 80px;
    text-align: center;
    font-size: 33px;
    margin: 0 auto;
    color: #FFFFFF;
  }
  .close {
    width: 60px;
    height: 60px;
    position: absolute;
    right: 20px;
    top: 20px;
    background: url('../../../assets/img/share/close.png') no-repeat center;
    background-size: 32px 32px;
  }
}
</style>



